* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  font: inherit;
  font-size: 10px;
  text-decoration: none;
  vertical-align: baseline;
  line-height: 1;
  list-style: none;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
}

html {
  max-width: 47rem;
  background-color: black;
}

@media screen and (max-width: 700px) {
  html {
    max-width: 100vw;
  }
