@import '../../Styles/colors';

main {
    display: flex;
    flex-direction: column;
    gap: 2rem;

    background-color: black;
    padding: 8rem 2rem 2rem 2rem;
}

section.widget {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;

    padding: 2rem;
    border-radius: 1rem;
    background-color: $color-darker;
    
    &.options {
        width: 100%;
    }

    div.widget-block.title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        div.title {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            gap: 1rem;

            div {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 1rem;

                i {
                    font-size: 1.8rem;
                    color: $color-primary;
                }
                
                h3 {
                    font-size: 2.4rem;
                    font-weight: 600;
                    color: $color-lighter;
                }
            }
            h4 {
                font-size: 1.4rem;
                font-weight: 400;
                color: $color-light;
            }
        }

        div.info {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            gap: 1rem;

            i {
                border-radius: 50%;
                padding: 1rem;
                font-size: 1.4rem;
                color: $color-good;
                background-color: $color-good-dark;
            }

            p {
                font-size: 1.2rem;
                font-weight: 500;
                color: $color-good;
            }
        }
            
    }

    div.widget-block.score-card {

        div.score-card {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;
            padding: 2rem;

            svg {
                width: 50%;
            }

            h3 {
                font-size: 2.4rem;
                font-weight: 600;

                &.bad {color: $color-primary;}
                &.medium {color: $color-secondary;}
                &.good {color: $color-good;}
                &.great {color: $color-great;}
            }

            p {
                font-size: 1.6rem;
                font-weight: 400;
                color: $color-light;
            }
        }
    }

    div.widget-block.footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        div.info {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;

            p {
                font-size: 1.4rem;
                font-weight: 500;
                color: $color-light;
            }

            span {
                font-size: 1.4rem;
                font-weight: 500;
                color: $color-lighter;
            }
        }

        div.actions {
            button {
                font-size: 1.6rem;
                font-weight: 500;
                color: $color-lighter;

                width: 16rem;
                padding: 1.2rem 0;
                border-radius: 2rem;
                border: 1px solid $color-primary;
                background-color: $color-primary-dark;
            }
        }
    }

    div.tyres {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 1.6rem;
        width: 100%;

        div.widget-block.tyre {
            display: grid;
            align-items: center;
            justify-items: space-between;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr;
            gap: 1.6rem;
            
            padding: 1rem;
            border-radius: 1rem;
            border: 0px solid $color-darker;

            &.great {
                border: 1px solid $color-great;
                background-color: $color-great-dark;
                i {color: $color-great}
            }

            &.caution {
                border: 1px solid $color-secondary;
                background-color: $color-secondary-dark;
                i {color: $color-secondary}
            }

                div {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                i {
                    font-size: 1.6rem;
                }

                p {
                    font-size: 1.4rem;
                    font-weight: 500;
                    color: $color-light;
                }
                
                span {
                    font-size: 1.4rem;
                    font-weight: 500;
                    text-align: right;

                    &.great   {color: $color-great}
                    &.caution {color: $color-secondary}
                }
            }
                
        }
    }

    div.readings {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.4rem;
        width: 100%;

        div.widget-block.reading {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            width: 100%;
            gap: 1rem;
            padding: 2rem;
            border-radius: 2rem;
            background-color: $color-dark-2;

            i, span {
                &.low {background-color: $color-great-dark; color: $color-great;}
                &.medium {background-color: $color-secondary-dark; color: $color-secondary;}
                &.high {background-color: $color-primary-dark; color: $color-primary;}
                &.unknown {background-color: $color-good-dark; color: $color-good;}
            }

            div.icon {
                i {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 1.6rem;
                    width: 3.2rem;
                    height: 3.2rem;
                    border-radius: 50%;
                }
            }

            div.content {
                display: flex;
                flex-direction: column;
                gap: 1rem;
                width: 100%;

                div.title {
                    display: flex;
                    align-items: flex-start;
                    justify-content: space-between;

                    h3 {
                        font-size: 1.6rem;
                        font-weight: 500;
                        color: $color-lighter;
                        line-height: 150%;
                    }

                    span {
                        font-size: 1.4rem;
                        font-weight: 600;
                        padding: .6rem 1.2rem;
                        border-radius: 10rem;
                    }
                }

                div.description {
                    p {
                        color: $color-light;
                        font-size: 1.4rem;
                        line-height: 150%;
                        text-align: justify;

                        &.unknown {line-height: 200%;}

                        button {
                            margin-left: 1rem;
                            font-size: 1.4rem;
                            padding: .6rem 1.2rem;
                            border-radius: 10rem;
                            color: $color-primary;
                            border: 1px solid $color-primary;
                            background-color: $color-primary-dark;
                        }
                    }
                }

                div.status {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    span {
                        font-size: 1.2rem;
                        font-weight: 600;
                        padding: .6rem 1.2rem;
                        border-radius: 10rem;
                    }

                    p {
                        font-size: 1rem;
                        font-weight: 600;
                        color: $color-light;

                        i {
                            margin-right: .6rem;
                        }
                    }
                }
            }
        }
    }
}